.Tilt {
	background: linear-gradient(85deg, #ff5edf 0%, #04c8de 100%);
}
.form {
	width: 700px;
	background-color:#def;
background-image: radial-gradient(closest-side, transparent 98%, rgba(0,0,0,.3) 99%),
radial-gradient(closest-side, transparent 98%, rgba(0,0,0,.3) 99%);
background-size:80px 80px;
background-position:0 0, 40px 40px;
}
.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	/*display: flex;
	flex-wrap: wrap;
	justify-content: center;*/
	cursor: pointer;
}

.image-container {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	max-width: 100%;
	max-height: 100%;
	display: flex;
}

.bounding-boxes {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

#modal-root {
	position: relative;
	z-index: 999;
}

.profile-modal {
	background-color: rgba(255,255,255,0.8);
	position: fixed;
	width: 100%;
	height: 100%;
	top:0;
	right:0;
	bottom:0;
	left:0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.profile-modal article  {
	position: relative;
}

.modal-close {
	position: absolute;
	top: 10px;
	right: 10px;
	background-color: transparent;
	border: none;
	font-size: 3rem;
}
.modal-close:hover {

}

.App {
  text-align: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'courier new', courier, monospace;/*-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(85deg, #ff5edf 0%, #04c8de 100%);
}

.center {
  display: flex;
  justify-content: center;
}

button {
  cursor: pointer;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Bootstrap overrides */
.hover-bg-black:hover,
.hover-bg-black:focus {
  background-color: #000 !important;
}
.hover-white:hover {
  color: #fff !important;
}


